import React, {useState, useEffect} from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faBars, faXmark, faArrowDown, faArrowUp, faArrowRight } from '@fortawesome/free-solid-svg-icons';
import mainCategories from '../categories/mainCategories';

import { Link } from 'react-router-dom';

import MenuSubcategory from '../components/MenuSubcategory';
import logo from '../assets/Standard Logo Files/Original on Transparent.png'

function Menu() {
    const [displayMenu, setDisplayMenu] = useState(false)
    const [displaySearchOption, setDisplaySearchOption] = useState(false)
    const [displayOfferOption, setDisplayOfferOption] = useState(false)

    const [scrollDirection, setScrollDirection] = useState('up');
    const [prevScrollY, setPrevScrollY] = useState(0);
  
    const handleScroll = () => {
      const currentScrollY = window.scrollY;
  
      if (currentScrollY > prevScrollY) {
        setScrollDirection('down');
      } else if (currentScrollY < prevScrollY) {
        setScrollDirection('up');
      }
  
      setPrevScrollY(currentScrollY);
    };
  
    useEffect(() => {
      window.addEventListener('scroll', handleScroll);
  
      return () => {
        window.removeEventListener('scroll', handleScroll);
      };
    }, [prevScrollY]);


    const displaySearch = () => {
        setDisplaySearchOption(!displaySearchOption)
        setDisplayOfferOption(false)
    }

    const displayOffer = () => {
        setDisplayOfferOption(!displayOfferOption)
        setDisplaySearchOption(false)
    }

    return ( 
        <nav className={`navbar ${displayMenu ? 'navbar--active' : ''} ${scrollDirection}`}>
            <div className="navbar__mobile-icons" onClick={() => setDisplayMenu(!displayMenu)}>
                <FontAwesomeIcon icon={faBars} className={displayMenu ? 'no-active' : ''}/>
                <FontAwesomeIcon icon={faXmark} className={displayMenu ? '' : 'no-active'}/>
            </div>

            <div className="navbar__logo-container">
                <a href="#"><img src={logo} alt="logo" className="navbar__logo" /></a>
            </div>

            <div className="navbar__options-container">
                <ul className="navbar__options-main">
                    <li className="navbar__option">
                        <p>TrendCo Business</p>
                    </li>
                    <li className="navbar__option">
                        <p onClick={displaySearch}>
                            Szukam <span> </span> 
                            {displaySearchOption ? <FontAwesomeIcon icon={faArrowUp} /> : <FontAwesomeIcon icon={faArrowDown} />}
                        </p>
                        <MenuSubcategory text={mainCategories} displaySubcategory={displaySearchOption}/>
                    </li>
                    <li className="navbar__option">
                        <p onClick={displayOffer}>
                            Oferuj <span> </span>
                            {displayOfferOption ? <FontAwesomeIcon icon={faArrowUp} /> : <FontAwesomeIcon icon={faArrowDown} />}
                        </p>
                        <MenuSubcategory text={mainCategories} displaySubcategory={displayOfferOption}/>
                    </li>
                    <li className="navbar__option">
                        <p>Forum</p>
                    </li>
                </ul>

                <div className="navbar__options-login-register">
                    <Link to="/login" className='navbar__login'>Logowanie</Link>
                    <Link to="/register" className='navbar__register'>Rejestracja</Link>
                </div>
            </div>

        </nav>
     );
}

export default Menu;