import * as Yup from 'yup'

const validationSchema = Yup.object().shape({
    username: Yup.string().required('To pole jest wymagane').min(6, 'Minimum 6 znakw').max(20, 'Maksymalnie 20 znakw'),
    name: Yup.string().required('To pole jest wymagane').min(3, 'Minimum 3 znaki').max(20, 'Maksymalnie 20 znakw'),
    surname: Yup.string().min(3, 'Minimum 3 znaki').max(20, 'Maksymalnie 20 znakw'),
    email: Yup.string().required('To pole jest wymagane').email('Nieprawidowy adres email'),
    password: Yup
        .string()
        .required('To pole jest wymagane')
        .min(8, 'Minimum 8 znakw')
        .max(25, 'Maksymalnie 25 znakw')
        .matches(
        /^(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]+$/,
        'Haso musi zawiera przynajmniej jedn wielk liter, jedn cyfr i jeden znak specjalny'
        ),
    confirmPassword: Yup
        .string()
        .oneOf([Yup.ref('password'), null], 'Hasa nie s identyczne')
        .required('To pole jest wymagane'),
    terms: Yup.bool().oneOf([true], 'Zaakceptowanie regulaminu oraz polityki prywatnoci jest wymagane')
})

export default validationSchema