import validationSchema from "./validation";
import { useFormik } from "formik";
import { Button, TextField, Checkbox, FormControlLabel, FormHelperText,} from "@mui/material";

import LogoContainer from "../components/LogoContainer";
import Logo from '../assets/Standard Logo Files/Original on Transparent.png'
import { Link } from "react-router-dom";

function RegisterForm() {
    const formik = useFormik({
        initialValues: {
            username: '',
            name: '',
            surname: '',
            email: '',
            password: '',
            confirmPassword: '',
            terms: false,
        },
        validationSchema: validationSchema,
        onSubmit: (values, {resetForm}) => {
            resetForm()
        }
    })

    return ( 
        <>
            <div className="registerform__container">
            <LogoContainer />
            <div className="registerform__desktop">
                <h2 className="registerform__desktop--title">Witaj!</h2>
                <p className="registerform__desktop--text">30 sekund - tyle dzieli Ci od zanurzenia si w wiat ofert na TrendCo</p>
                <div className="registerform__desktop--image-container">
                    <img src={Logo} alt="" />
                </div>
                <h4 className="registerform__desktop--slogan">PASJA. <br /> TO WICEJ NI WSZYSTKO.</h4>
            </div>
            <div className="registerform">
                <form onSubmit={formik.handleSubmit} className="registerform__form">
                    <h3 className="registerform__title">Za darmowe konto</h3>
                    <TextField 
                        fullWidth
                        variant="standard"
                        margin="dense"
                        id="username"
                        name="username"
                        label="Nazwa uytkownika"
                        value={formik.values.username}
                        onChange={formik.handleChange}
                        onBlur={formik.handleBlur}
                        error={formik.touched.username && Boolean(formik.errors.username)}
                        helperText={formik.touched.username && formik.errors.username}
                        className="registerform__textfield"
                    />

                    <TextField 
                        fullWidth
                        variant="standard"
                        margin="dense"
                        id="name"
                        name="name"
                        label="Imi"
                        value={formik.values.name}
                        onChange={formik.handleChange}
                        onBlur={formik.handleBlur}
                        error={formik.touched.name && Boolean(formik.errors.name)}
                        helperText={formik.touched.name && formik.errors.name}
                        className="registerform__textfield"
                    />

                    <TextField 
                        fullWidth
                        variant="standard"
                        margin="dense"
                        id="surname"
                        name="surname"
                        label="Nazwisko"
                        value={formik.values.surname}
                        onChange={formik.handleChange}
                        onBlur={formik.handleBlur}
                        error={formik.touched.surname && Boolean(formik.errors.surname)}
                        helperText={formik.touched.surname && formik.errors.surname}
                        className="registerform__textfield"
                    />

                    <TextField 
                        type="email"
                        fullWidth
                        variant="standard"
                        margin="dense"
                        id="email"
                        name="email"
                        label="Adres e-mail"
                        value={formik.values.email}
                        onChange={formik.handleChange}
                        onBlur={formik.handleBlur}
                        error={formik.touched.email && Boolean(formik.errors.email)}
                        helperText={formik.touched.email && formik.errors.email}
                        className="registerform__textfield"
                    />

                    <TextField 
                        type="password"
                        fullWidth
                        variant="standard"
                        margin="dense"
                        id="password"
                        name="password"
                        label="Haso"
                        value={formik.values.password}
                        onChange={formik.handleChange}
                        onBlur={formik.handleBlur}
                        error={formik.touched.password && Boolean(formik.errors.password)}
                        helperText={formik.touched.password && formik.errors.password}
                        className="registerform__textfield"
                    />

                    <TextField 
                        type="password"
                        fullWidth
                        variant="standard"
                        margin="dense"
                        id="confirmPassword"
                        name="confirmPassword"
                        label="Powtrz haso"
                        value={formik.values.confirmPassword}
                        onChange={formik.handleChange}
                        onBlur={formik.handleBlur}
                        error={formik.touched.confirmPassword && Boolean(formik.errors.confirmPassword)}
                        helperText={formik.touched.confirmPassword && formik.errors.confirmPassword}
                        className="registerform__textfield"
                    />

                    <FormControlLabel
                        control={
                        <Checkbox
                            size="large"
                            name="terms"
                            checked={formik.values.terms}
                            onChange={formik.handleChange}
                            onBlur={formik.handleBlur}
                        />
                        }
                        label="Akceptuj regulamin oraz polityk prywatnoci"
                    />
                    {formik.touched.terms && formik.errors.terms && (
                        <FormHelperText error margin="dense">{formik.errors.terms}</FormHelperText>
                    )}

                    <Button type="submit" fullWidth variant="contained" style={{ marginTop: '20px' }}>Zarejestruj</Button> 

                    <p className="registerform__login">Masz ju konto? <Link to='/login'>Zaloguj si</Link></p>

                </form>


            </div>
            </div>
        </>
     );
}

export default RegisterForm;